.cart {
	background-color: $white;
	padding: 69px 0 109px;

	@include mq(1800px) {
		padding: 69px 0 100px;
	}
	
	@include mq(1500px) {
		padding: 69px 0 80px;
	}

	@include mq($lap-large) {
		padding: 60px 0;
	}

	@include mq($xl) {
		padding: 40px 0;
	}
}

.cart__top {
	display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

	@include mq($lg) {
		flex-wrap: wrap;
		justify-content: center;

		.cart__title {
			width: 100%;
			order: 1;
		}
	}
}

.cart__container {
	display: flex;
	flex-direction: column;
	text-align: center;
	width: 100%;
}

.cart__top-btn {
	align-self: flex-end;
	@include mq($lg) {
		order: 2;
		margin: 0 10px;
	}

	@include mq($md) {
		margin: 0 3px 15px;
	}
}

.cart__item {
	background-color: $grey-mid;
	padding: 44px 75px 28px 91px;
	display: flex;
	width: 100%;
    margin-bottom: 23px;
	position: relative;
	max-width: 1344px;

	@include mq($lap-large) {
		padding: 35px 60px;
	}

	@include mq($xl) {
		padding: 35px 40px;
	}

	@include mq($lg) {
		padding: 25px;
	}
	
	@include mq($md) {
		flex-wrap: wrap;
		max-width: 300px;
		justify-content: center;
		flex-direction: column;
		margin: 0 10px 20px;
		align-items: center;
	}

	@include mq(700px) {

	}
}

.cart__wrapper {
	margin-top: 72px;
	padding: 0 11px;

	@include mq($lap-large) {
		margin-top: 40px;
	}

	@include mq($xl) {
		margin-top: 20px;
	}

	@include mq($md) {
		margin: 0 -15px 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
	}
}

.cart__item-details {
	margin: 2px 0 0 85px;
    text-align: left;
    max-width: 28.8%;
    width: 100%;

	@include mq($lap-large) {
		margin: 0;
		max-width: 30%;
	}

	@include mq($xl) {
		max-width: 35%;
	}

	@include mq($md) {
		max-width: 100%;
		text-align: center;

	}

	span {
		display: block;
		color: $black;
		text-transform: uppercase;
		line-height: 22px;
		font-family: $font-second;
		font-weight: normal;

		&.cart__item-name {
			font-size: 22px;
			margin-bottom: 12px;
			font-family: $font-main;
		
			@include mq($lap-large) {
				font-size: 20px;
			}
		
			@include mq($xl) {
				font-size: 18px;
				margin-bottom: 8px;
			}
		}
	}
}

.cart__item-info {
	font-family: $font-second;
	font-size: 16px;

	@include mq($xl) {
		font-size: 14px;
	}
}

.cart__item-button {
	margin: 0 62px;

	@include mq($lap-large) {
		margin: 0 40px;
	}

	@include mq($xl) {
		margin: 0 20px;
	}

	@include mq(700px) {
		max-width: 50%;
		margin: 15px 0 0;
		align-self: flex-end;
	}

	@include mq($md) {
		max-width: 100%;
		margin: 15px 0;
		align-self: center;
	}
}

.cart__item-body-image {
	display: flex;
	align-items: center;
	justify-content: center;
	max-width: 152px;
	height: 118px;
	width: 100%;
	margin-left: 42px;

	@include mq($lap-large) {
		margin: 0 auto;
		max-width: 140px;
	}

	@include mq($lg) {
		margin: 0 15px;
	}

	@include mq($md) {
		margin: 0 auto;
	}
	
	img {
		width: 100%;
		display: block;
		height: auto;
		@include mq($lap-large) {
			max-width: 80%;
		}
	}
}

.cart__item-price {
	align-self: flex-end;
	margin-left: auto;
	margin-bottom: 13px;

	@include mq($lap-large) {
		margin-bottom: 0;
	}

	@include mq($lg) {
		position: absolute;
		right: 30px;
		bottom: 20px;
		margin-left: 0;
	}

	@include mq($md) {
		position: relative;
		right: auto;
		bottom: auto;
		margin-top: 0;
		max-width: 100%;
	}
}

.cards__price {
	font-family: $font-second;
	line-height: 30px;
	color: $black;
	text-transform: uppercase;
	font-size: 16px;
	font-weight: 600;

	@include mq($lap-large) {
		font-size: 15px;
	}

	@include mq($xl) {
		font-size: 14px;
	}
	span {
		font-size: 22px;
		margin-left: 7px;
		font-weight: bold;

		@include mq($lap-large) {
			font-size: 20px;
		}
	
		@include mq($xl) {
			font-size: 18px;
		}
	}
}

.cart__total {
	margin-left: auto;
    margin-top: 9px;
    margin-right: 79px;
	width: 100%;
	display: flex;
	justify-content: flex-end;

	@include mq($lap-large) {
		margin-right: 59px;
	}

	@include mq($xl) {
		margin-right: 39px;
	}

	@include mq($lg) {
		margin-right: 30px;
	}

	@include mq($md) {
		margin-right: 0;
	}
}

.cards__total-price {
	font-weight: 600;
	font-size: 16px;
	line-height: 22px;
	font-family: $font-second;
	color: $black;
	text-transform: uppercase;

	span {
		font-size: 22px;
		font-weight: bold;
		margin-left: 8px;
	}
}