@keyframes puff-in-center {
  0% {
    transform: scale(0.5);
    filter: blur(2px);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    filter: blur(0);
    opacity: 1;
  }
}

@keyframes puff-in-middle {
  0% {
    transform: scale(1.4);
    filter: blur(2px);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    filter: blur(0);
    opacity: 1;
  }
}

@keyframes float {
  0% {
    transform: rotate(-1deg) translate(0, 0);
  }
  100% {
    transform: rotate(-7deg) translate(10%, 10%);
  }
}

@keyframes float-2 {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(10%);
  }
}

@keyframes float-3 {
  0% {
    transform: rotate(-4deg) translateY(0);
  }
  100% {
    transform: rotate(10deg) translateY(-10%);
  }
}

.hamburger {
  cursor: pointer;
  width: 30px;
  height: 19px;
  position: relative;
  z-index: 1002;
}

@media screen and (max-width: 1199px) {
  .hamburger {
    display: block;
  }
}

.hamburger::after, .hamburger::before,
.hamburger span {
  width: 100%;
  height: 3px;
  position: absolute;
  display: block;
  background-color: #fff;
  pointer-events: none;
  border-radius: 4px;
}

.hamburger::after, .hamburger::before {
  content: '';
  transition: 350ms width 350ms ease;
}

.hamburger::after {
  top: 0;
  left: 0;
  width: 100%;
}

.hamburger::before {
  bottom: 0;
  left: 0;
  width: 60%;
}

.hamburger span {
  width: 80%;
  top: 50%;
  margin-top: -1.5px;
  transition: transform 350ms ease;
}

.hamburger span::before {
  content: '';
  background-color: #fff;
  width: 100%;
  height: 3px;
  position: absolute;
  left: 0;
  z-index: 1;
  transition: transform 350ms ease;
  border-radius: 4px;
}

.site-header__hamburger {
  display: none;
}

@media screen and (max-width: 1199px) {
  .site-header__hamburger {
    display: block;
    margin-left: 60px;
    margin-right: 15px;
  }
}

@media screen and (max-width: 991px) {
  .site-header__hamburger {
    margin-left: 40px;
    margin-right: 5px;
  }
}

@media screen and (max-width: 575px) {
  .site-header__hamburger {
    margin-left: 30px;
  }
}

.menu-open .hamburger::after, .menu-open .hamburger::before {
  transition: 350ms ease;
  width: 0;
}

.menu-open .hamburger::after {
  left: 0;
}

.menu-open .hamburger::before {
  right: 0;
}

.menu-open .hamburger span {
  width: 100%;
  transform: rotate(45deg);
  transition: 350ms transform 350ms ease;
}

.menu-open .hamburger span::before {
  transform: rotate(-90deg);
  transition: 350ms transform 350ms ease;
}

