  display: none;
  width: 30px;
  height: 19px;
  right: 15px;
  top: 21px;
  position: absolute;
  z-index: 110;
}

.menu-btn::after, .menu-btn::before,
.menu-btn span {
  width: 100%;
  height: 3px;
  position: absolute;
  display: block;
  background-color: #fff;
}

.menu-btn::after, .menu-btn::before {
  content: '';
  transition: 350ms width 350ms ease;
}

.menu-btn::after {
  top: 0;
  left: 0;
}

.menu-btn::before {
  bottom: 0;
  right: 0;
}

.menu-btn span {
  top: 50%;
  margin-top: -1.5px;
  transition: transform 350ms ease;
}

.menu-btn span::before {
  content: '';
  background-color: #fff;
  width: 100%;
  height: 3px;
  position: absolute;
  left: 0;
  z-index: 1;
  transition: transform 350ms ease;
}

