/*--------------------------------------------------------------
## Buttons
--------------------------------------------------------------*/
.btn {
    font-size: 17px;
    line-height: 20px;
    font-family: $font-main;
    color: $black;
    text-transform: uppercase;
    border-radius: 22px;
    background-color: $white;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $white;
    padding: 13px 15px;
    transition: $dur $ease;
    max-width: 181px;
    min-width: 181px;
    width: 100%;

    @include mq($lap-large) {
		font-size: 16px;
        min-width: none;
	}

	@include mq($xl) {
		font-size: 15px;
	}

    @include mq($lg) {
		min-width: none;
	}

    @include mq($sm) {
		font-size: 12px;
        min-width: 130px;
        padding: 10px;
	}

    @include hover {
        background-color: $orange;
        color: $white;
    }

    &.btn--black {
        background-color: $black;
        color: $white;

        @include hover {
            background-color: $orange;
        }
    }

    &.btn--sm {
        font-size: 12px;
        max-width: 155px;
        min-width: 155px;
        padding: 6px 10px;
    }

    &.btn--big {
        max-width: 217px;
        min-width: 217px;
        padding: 15.5px 15px;

        @include mq($sm) {
            padding: 10px;
        }
    }
}