/*--------------------------------------------------------------
## Navigation
--------------------------------------------------------------*/
a {
	text-decoration: none;

	&:hover,
	&:focus,
	&:active {
		text-decoration: none;
		outline: 0;
	}
}

.main-nav__list-link {
	color: $white;
	font-family: $font-main;
	font-size: 18px;
	line-height: 21px;
	text-transform: uppercase;
	transition: $dur $ease;
	position: relative;

	img {
		display: none;
	}
}

.main-nav {
	display: flex;
	align-items: center;

	@include mq($xl) {
		position: absolute;
		right: 0;
		top: 0;
		width: 300px;
		height: 100vh;
		background-color: $black;
		padding: 100px 30px;
		flex-direction: column;
		transform: translateX(100%);
		transition: $dur $ease;
		z-index: 1001;
	}
}

.menu-open {
	position: relative;
	.main-nav {
		transform: translateX(0);
	}

	.header__overlay {
		&::before {
			opacity: 1;
			visibility: visible;
		}
	}
}

.header__overlay {
	position: absolute;
    width: 100%;
    height: 100vh;
	pointer-events: none;
    left: 0;
	&::before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($white, .5);
		opacity: 0;
		visibility: hidden;
		transition: $dur $ease;
		z-index: 1000;
	}
}

.main-nav__list {
	display: flex;
	align-items: center;
	width: 100%;
	@include mq($xl) {
		flex-direction: column;
	}
}

.main-nav__list-item {
	margin-right: 75px;

	@include mq($lap-large) {
		margin-right: 40px;
	}

	@include mq($xl) {
		padding: 20px 0;
		margin: 0;
	}

	@include hover {
		a {
			color: $orange;
		}
	}
}

.main-nav__list-item--image {
	margin: 0 70px;

	@include mq($xl) {
		display: none;
	}
}

.main-nav-cart {
	position: relative;

	@include mq($xl) {
		padding: 20px 0;
		margin-left: auto;
	}

	@include hover {
		a {
			color: $orange;
		}

		.main-nav-cart-qty {
			background-color: $white;
			color: $orange;
		}
	}
}

.logo-mobile {
	display: none;

	@include mq($xl) {
		display: block;
		max-width: 300px;
		img {
			width: 100%;
			height: 100%;
		}
	}

	@include mq($lg) {
		max-width: 200px;
	}

	@include mq($sm) {
		max-width: 130px;
	}
}

.main-nav-cart-link {
	color: $white;
	font-family: $font-main;
	font-size: 18px;
	line-height: 21px;
	text-transform: uppercase;
	position: relative;
	transition: $dur $ease;

	@include mq($lap-large) {
		margin-right: 15px;
	}
}

.main-nav-cart-qty {
	background-color: $orange;
	padding: 2px;
	min-width: 25.5px;
	min-height: 24px;
	height: auto;
    position: absolute;
    right: -36px;
    top: -14px;
    color: $black;
    border-radius: 50%;
    display: flex;
	align-items: center;
	justify-content: center;
	transition: $dur $ease;

	@include mq($lap-large) {
		right: -18px;
	}
	
	@include mq($xl) {
		top: 8px;
		min-width: 20px;
		min-height: 20px;
		padding: 0;
		font-size: 14px;
	}

	@include mq($lg) {
		right: -9px;
	}
}

.main-navigation {
	.primary-menu {
		list-style: none;
		margin: 0;
		padding: 0;

		li:hover > ul,
		li.focus > ul {
			transform: translateY(0);
			opacity: 1;
		}
	}

	.sub-menu {
		position: absolute;
		left: 0;
		top: 100%;
		border-top: 3px solid $white;
		z-index: 99999;
		background-color: $black;
		transition: $dur $ease;
		transform: translateY(5px);
		opacity: 0;

		.sub-menu {
			left: 100%;
			top: 0;
		}

		li {
			border-bottom: 1px solid $black;

			&:last-of-type {
				border-bottom: none;
			}
		}

		a {
			width: 200px;
		}
	}

	li {
		position: relative;
		float: left;
		transition: $dur $ease;

		&:hover > a,
		&.focus > a {
			background-color: $white;
		}

		@include hover {
			a {
				color: $grey;
			}
		}
	}

	a {
		color: $white;
		font-family: $font-main;
		font-size: 18px;
		line-height: 21px;
		text-transform: uppercase;
		transition: $dur $ease;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
	}

	@include mq($sm) {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		visibility: hidden;
		z-index: 100;
		transition: $dur $ease $dur;

		&::before {
			content: '';
			display: block;
			background-color: rgba($black, .4);
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			opacity: 0;
			transition: $dur $ease;
		}

		&.open {
			visibility: visible;
			transition: none;

			&::before {
				opacity: 1;
				transition: $dur $ease 10ms;
			}

			> ul {
				transform: translateX(0);
				transition: $dur $ease 10ms;
			}
		}

		.primary-menu {
			position: absolute;
			top: 0;
			right: 0;
			width: 230px;
			height: 100%;
			background-color: $black;
			padding-top: 60px;
			overflow: auto;
			z-index: 100;
			transform: translateX(100%);
			transition: $dur ease;
		}

		.sub-menu {
			position: static;
			transition: initial;
			transform: none;
			background-color: transparent;
			border: 0;
			padding: 5px 0 10px 10px;
			opacity: 1;
			display: none;

			li {
				border: 0;
				font-size: .85em;
			}

			.sub-icon {
				top: 1px;
			}
		}

		li {
			float: none;

			&:hover > a,
			&.focus > a {
				background-color: transparent;
			}
		}

		a {
			display: inline-block;
			padding: 0 15px;
		}

		.sub-icon {
			line-height: 1;
			display: block;
			color: $white;
			font-size: 18px;
			position: absolute;
			right: 60px;
			top: 3px;
			transition: $dur $ease;
		}
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 1.5em;
		overflow: hidden;
	}

	.nav-previous {
		float: left;
		width: 50%;
	}

	.nav-next {
		float: right;
		text-align: right;
		width: 50%;
	}
}
