.cards {
	background-color: $white;
	padding: 69px 0 84px;

	@include mq($lap-large) {
		padding: 60px 0;
	}

	@include mq($xl) {
		padding: 40px 0;
	}
}

.cards__container {
	display: flex;
	flex-direction: column;
	text-align: center;
	width: 100%;
}

.cards__item {
	background-color: $grey-mid;
	padding: 44px 31px 48px 35px;
    margin-bottom: 26px;
	position: relative;

	@include mq($lap-large) {
		padding: 40px 30px;
	}

	@include mq($md) {
		padding: 30px 20px;
		max-width: 380px;
		margin: 0 auto 20px;
	}
}

.cards__wrapper {
	margin-top: 64px;

	@include mq($lap-large) {
		margin-top: 40px;
	}

	@include mq($xl) {
		margin-top: 20px;
	}
}

.cards__item-body {
	margin-bottom: 25px;

	@include mq($lg) {
		margin-bottom: 20px;
	}
	span {
		display: block;
		color: $black;
		text-transform: uppercase;
	}
}

.cards__item-body-image {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 312px;
	margin-bottom: 35px;

	@include mq($lap-large) {
		height: 250px;
		margin-bottom: 20px;
	}

	@include mq($xl) {
		height: 200px;
	}

	@include mq($md) {
		height: auto;
		max-width: 85%;
		margin: 0 auto 15px;
	}

	@include mq($sm) {
		max-width: 70%;
	}
}

.cards__item-body-name {
	font-family: $font-main;
	font-size: 22px;
	line-height: 26px;
	margin-bottom: 9px;

	@include mq($lap-large) {
		font-size: 20px;
	}

	@include mq($xl) {
		font-size: 18px;
	}
}

.cards__item-body-price {
	font-weight: bold;
	font-size: 22px;
	line-height: 30px;
	font-family: $font-second;

	@include mq($lap-large) {
		font-size: 20px;
	}

	@include mq($xl) {
		font-size: 18px;
	}
}

//
.cards__item-footer {
	display: flex;
	justify-content: center;
	width: 100%;
}
.quantity {
	position: relative;
	display: flex;
	@include mq($lg) {
		margin-right: 10px;
	}

	input {
		border-radius: 22px;
		width: 61px;
		height: 44px;
		border: none;
		background-color: $white;
		padding: 10px;
		text-align: center;
		font-size: 25px;

		@include mq($sm) {
			width: 54px;
			padding: 8px;
		}
	}
}

.quantity__button {
	margin-left: 11px;
	margin-right: 13px;

	@include mq($xl) {
		margin: 0 10px;
	}

	@include mq($lg) {
		display: none;
	}
}

.quantity-button {
	background-color: $black;
	color: $white;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 50%;
	padding: 5px;
	height: 22px;
	width: 22px;
	cursor: pointer;
	transition: $dur $ease;
	border: none;
	font-size: 16px;
	position: relative;

	@include hover {
		background-color: $orange;
	}

	svg {
		fill: $white;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		transition: $dur $ease;
		height: 15px;
	}
}

.quantity-remove {
	span {
		transform: translate(-50%, -56%);
	}
}

.quantity-add {
	margin-bottom: 2px;
	position: relative;
}

.heart {
	width: 44px;
	min-width: 44px;
	height: 44px;
	background-color: $black;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	margin-left: 14px;
	cursor: pointer;

	@include mq($xl) {
		position: absolute;
		right: 15px;
		top: 15px;
	}

	@include mq($lg) {
		position: relative;
		right: auto;
		top: auto;
		margin-left: 10px;
	}

	@include hover {
		.heart__full {
			opacity: 1;
			visibility: visible;
		}

		.heart__empty {
			opacity: 0;
			visibility: hidden;
		}
	}

	&.active {
		.heart__full {
			opacity: 1;
			visibility: visible;
		}

		.heart__empty {
			opacity: 0;
			visibility: hidden;
		}
	}
}

.heart-icon {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	transition: $dur $ease;
	width: 20px;
	height: auto;
}

.heart__full {
	opacity: 0;
	visibility: hidden;
	width: 23px;
}