/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
.footer {
	background-color: $black;
	padding: 43px 0;
}

.footer__logo {
	display: block;
	max-width: 227px;
	width: 100%;
	margin: 0 auto 20px;

	img {
		width: 100%;
		height: 100%;
	}
}

.copyright {
	text-align: center;
	font-size: 14px;
	line-height: 16px;
	font-family: $font-main;
	color: $grey;

	@include mq($sm) {
		font-size: 13px;

		span {
			display: block;

			&.copyright-line {
				display: none;
			}
		}
	}
}

.footer__wrap {
	width: 100%;
	display: flex;
	flex-direction: column;
}
