/*--------------------------------------------------------------
## Base Layout
--------------------------------------------------------------*/
html, body {
	@include font-smoothing(antialiased);
	height: 100%;
}

html {
	box-sizing: border-box;
}

body {
	padding-top: 224px;

	@include mq($xl) {
		padding-top: 150px;
	}

	@include mq($lg) {
		padding-top: 100px;
	}

	@include mq($sm) {
		padding-top: 80px;
	}
}

.wrap {
	max-width: 1370px;
	display: flex;
	width: 100%;
	align-items: center;
	margin: 0 auto;
	padding: 0 15px;
}

.site-main {
	// 100vh - header + footer
	min-height: calc(100vh - 170px);

	@include mq($xl) {
		min-height: calc(100vh - 130px);
	}
}

.page-header {
	margin: 50px 0;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}
